* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
}

.app .background {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url('/public/roka_jade.png') no-repeat center center/cover;
  width: 100vw;
  height: 90.57vw;
  max-height: 100vh;
  max-width: 110.3vh;
}

.app .background .title {
  position: absolute;
  top: 23%;
  left: 61.5%;
  width: 30%;
  height: 5%;
  font-size: 2.9vmin;
}

.app .background .time {
  position: absolute;
  display: flex;
  justify-content: space-between;
  top: 30%;
  left: 61.5%;
  width: 29%;
  height: 8%;
}

.app .background .timesplit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20%;
  height: 100%;
  background-color: rgb(224, 224, 224);
  border: 1px solid black;
  border-radius: 10px;
}

.app .background .timesplit .number {
  padding: 10% 20% 10% 20%;
  border-radius: 10px;
  font-size: 2.9vmin;
  background-color: rgb(250, 250, 250);
}

.app .background .timesplit .unit {
  font-size: 1.5vmin;
}

.app .background .madeby {
  position: absolute;
  top: 44%;
  left: 85%;
  transform: translate(-50%, -50%);
  font-size: 1.5vmin;
}

.app .background .madeby a {
  text-decoration: none;
  color: royalblue;
}

.app .background .madeby a:hover {
  text-decoration: underline;
  COLOR: RED;
}